.wkt-sub-nav-container {
  font-family: MicrosoftYaHei;
  height: calc(100vh - 48px);
  overflow-y: auto;
  background-color: #f2f4f7;
  position: relative;
}
.wkt-sub-nav-container .sub-nav {
  position: fixed;
  top: 48px;
  left: 140px;
  width: 100px;
  height: 100%;
  box-sizing: border-box;
  background-color: white;
}
.wkt-sub-nav-container .sub-nav > div {
  height: 40px;
  text-align: start;
  line-height: 40px;
  font-size: 14px;
  color: #333333;
  cursor: pointer;
  box-sizing: border-box;
  padding-left: 16px;
}
.wkt-sub-nav-container .sub-nav > .active {
  background: #EBEDF0;
}
.wkt-sub-nav-container .sub-nav > .disabled {
  cursor: not-allowed;
}
.wkt-sub-nav-container .sub-nav-content-container {
  position: absolute;
  top: 0;
  left: 100px;
  height: calc(100vh - 48px);
  width: calc(100% - 100px);
}
.wkt-sub-nav-container .sub-nav-content-container.no-nav {
  left: 0;
  width: calc(100%);
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content {
  position: relative;
  margin: 10px 10px 0 10px;
  min-height: calc(100vh - 48px - 44px - 10px);
  background-color: white;
  box-sizing: border-box;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content.dark-nav-content {
  padding-top: 0;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .content-title {
  height: 50px;
  font-size: 20px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #333333;
  line-height: 50px;
  padding: 0 20px;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .real-content {
  position: relative;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .real-content.dark-bg {
  background-color: #f2f4f7;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .third-nav-item-container {
  padding: 10px 20px;
  background-color: white;
  box-sizing: border-box;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .third-nav-item-container .item {
  cursor: pointer;
  display: inline-block;
  padding: 5px 9px;
  box-sizing: border-box;
  border-radius: 2px;
  margin-right: 10px;
  font-size: 12px;
  background: #f5f7fa;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #999999;
  border: 1px solid transparent;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content .third-nav-item-container .active {
  border: 1px solid #256EFF;
  color: #256EFF;
}
.wkt-sub-nav-container .sub-nav-content-container .sub-nav-footer {
  height: 44px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #a8a8a8;
  line-height: 44px;
  text-align: center;
}
.scroll.wkt-sub-nav-container {
  height: calc(100vh - 48px - 17px);
}
.scroll.wkt-sub-nav-container .sub-nav-content-container {
  height: calc(100vh - 48px - 17px);
}
.scroll.wkt-sub-nav-container .sub-nav-content-container .sub-nav-content {
  min-height: calc(100vh - 48px - 44px - 17px - 10px);
}
